.card {
    font-family: 'Bungee', cursive;
    font-size: 1em;
    width: 10vw;
    height: 10vh;
  /*  font-family: 'Exo', sans-serif; */
    display: flex;           /* center in div */
    justify-content: center; /*               */
    align-content: center;   /*               */
    flex-direction: column;  /*               */
    
 /*   margin-left: 5%;
    margin-right: 5%;    */
    border: 10px inset var(--color3);
    border-radius: 5em;
    color: var(--color3);
    background-color: var(--color2);
    box-shadow: 0.6em 0.6em 2em 0.5em var(--color1), 0 0 0.4em 0.4em var(--color1); 
    box-shadow:0 0 10em 10em transparent;
    transition: color .25s, background-color .3s, box-shadow .4s, font-weight .35s, border .3s;
    animation-name: cardanimation;
    animation-duration: 10s;
    animation-iteration-count: infinite;

  }
  
  .card:hover {
    color: var(--color4);
    border: 12px outset var(--color4);
    background-color: var(--color1);
    box-shadow: 0 0 0.4em 0.4em var(--color4); 
    font-weight: bold;
  }
  
  .card:active {
    color: var(--color3);
    background-color: var(--color1);
    box-shadow: 0 0 0.4em 0.4em var(--color4);
    font-weight: bold;
    }

      /* The animation code */
  @keyframes cardanimation {
    0%   {background-color:var(--color1);}
    10%  {background-color:var(--color2);}
    20%  {background-color:var(--color1);}
    70%  {background-color:var(--color2);}
    75%  {background-color: var(--color5);}
    80%  {background-color:var(--color2);}
    100% {background-color:var(--color1);}
  }

  .buttoncolumn {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 3vh;
    gap: 3vh;
  }

  .sonoimage {
    zoom: 20%;
    opacity: 70%;
    padding: 0;
    margin: 0;

  }

  #grooveboxarea {
    grid-area: groovebox;
    align-self: end;
  }

  #soundsystemarea {
    grid-area: soundsystem;
    align-self: end;
  }

  #hostarea {
    grid-area: host;
    place-self: center;
    zoom: 180%;
  }

  #rulescard {
    grid-area: rules;
    place-self: center;
    align-self: end;
    zoom: 70%;
    margin-top: 10%;
  }

  .cardpartying {
    font-family: 'Bungee', cursive;
    font-size: 1em;
    width: 10vw;
    height: 10vh;
  /*  font-family: 'Exo', sans-serif; */
