/* The slider itself */
.slider {
    -webkit-appearance: none;  /* Override default CSS styles */
    -moz-appearance: none;
         appearance: none;
    width: 100%; /* Full-width */
    height: 10%; /* Specified height */
    background: var(--color2); /* Grey background */
    outline: none; /* Remove outline */
    opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */ /* 0.2 seconds transition on hover */
    transition: opacity .2s;
    margin-top: 7%;
    border: 1px solid var(--color1);
    border-radius: 30px;
}

/* Mouse-over effects */
.slider:hover {
    opacity: 1; /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    width: 1em; /* Set a specific slider handle width */
    height: 1em; /* Slider handle height */
    background: var(--color3); /* Green background */
    cursor: pointer; /* Cursor on hover */
    border-radius: 50%;
}

.slider::-moz-range-thumb {
    width: 1em; /* Set a specific slider handle width */
    height: 1em; /* Slider handle height */
    background: var(--color3); /* Green background */
    cursor: pointer; /* Cursor on hover */
    border-radius: 50%;
}

